ion-content {
  --padding-top: calc(max(var(--ion-safe-area-top), 24px) + 76px) !important;
  --padding-start: 16px;
  --background: white;
}
ion-item {
  --background: white;
}

ion-row {
  width: 100%;
  max-width: 1000px;
}

// Vehicle list styles
ion-list {
  ion-note {
    width: 50px;
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;

    li {
      display: flex;
      align-items: center;
      gap: 0.5rem;
    }
  }

  .ecl-padding-start {
    padding-left: 1rem;
  }
}

h6 {
  margin: 0.5rem 0;
  padding: 0 1rem;
}

// Loading spinner
ion-spinner {
  margin-top: 2rem;
  --color: var(--ion-color-primary);
}
